/**
 * @module ngeo.draw.module
*/
import ngeoBase from 'ngeo';
import ngeoDrawComponent from 'ngeo/draw/component';
import ngeoDrawController from 'ngeo/draw/Controller';
import ngeoDrawFeatures from 'ngeo/draw/features';
import ngeoDrawPoint from 'ngeo/draw/point';
import ngeoDrawRectangle from 'ngeo/draw/rectangle';
import ngeoDrawText from 'ngeo/draw/text';

/**
 * @type {!angular.Module}
 */
const exports = angular.module('ngeoDrawModule', [
  ngeoBase.module.name, // Change me when all dependencies are in a module.
  ngeoDrawController.module.name,
  ngeoDrawComponent.name,
  ngeoDrawFeatures.name,
  ngeoDrawPoint.name,
  ngeoDrawRectangle.name,
  ngeoDrawText.name,
]);


export default exports;



